@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
.flex_block {
  display: flex;
}

.block-InfoPod {
  padding: 2em 0;
}
.block-InfoPod .flex_block {
  justify-content: space-around;
}
.block-InfoPod .infopod {
  max-width: 30%;
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0 32px;
}
.block-InfoPod p {
  text-align: center;
  padding: 0;
  margin: 0;
}
.block-InfoPod img {
  box-sizing: border-box;
  background: #8dca31;
  height: auto;
  width: calc(64px + 1em);
  max-width: 100%;
  padding: 0.5em;
  display: block;
  border-radius: 50%;
  border: solid 3px #fff;
}
.block-InfoPod ul {
  display: flex;
  padding: 0;
  margin: 0;
}
.block-InfoPod ul li {
  flex: 1;
}

.block-Callout {
  box-shadow: inset 0 0 min(10vw, 150px) #000;
  padding: 3rem 0;
  text-align: center;
}
.block-Callout h2,
.block-Callout p {
  padding: 0;
  margin: 0;
}
.block-Callout hr {
  border-top: 1px dotted #8dca31;
  margin: 1em 0;
}

.row {
  display: flex;
  margin-left: -1rem;
  margin-right: -1rem;
}
.row .col {
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
  padding-left: 1rem;
  padding-right: 1rem;
}

header {
  padding: 1.5em 0;
  text-align: center;
}
header img {
  display: inline-block;
  margin: auto;
}

nav.main {
  border-bottom: 0.5em solid #8dca31;
}
nav.main a {
  position: relative;
  text-decoration: none;
  border: none;
  color: #fff;
  font-weight: 300;
}
nav.main .active a {
  font-weight: 600;
  color: #f7f7f7;
}
nav.main .active a::before {
  position: absolute;
  bottom: -0.9em;
  left: calc(50% - 0.5em);
  content: " ";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 0.5em 0.5em 0.5em;
  border-color: transparent transparent #8dca31 transparent;
}
nav.main ul {
  padding: 0 0 0.5em 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
}
nav.main ul li {
  display: block;
  padding: 0;
  margin: 0;
  list-style: none;
}

.banner_image picture {
  display: block;
  height: 100%;
  width: 100%;
  position: relative;
}
.banner_image picture::after {
  box-shadow: inset 0 0 min(10vw, 150px) #000;
  content: " ";
  display: block;
  height: 100%;
  position: absolute;
  top: 0;
  width: 100%;
}
.banner_image img {
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
  position: relative;
}

#footer {
  border-top: #8dca31 solid 0.25em;
}

.project-pods {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2em;
  padding-bottom: 2em;
}
.project-pods .pod {
  box-sizing: border-box;
  padding: 0;
  display: flex;
}
.project-pods .pod .pod-details {
  padding-left: 1em;
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  flex: 1;
}
.project-pods .pod .pod-details h2 {
  padding: 0 0 0.75em 0;
  margin: 0;
  line-height: 1em;
  font-size: 1.5em;
}
.project-pods .pod .pod-details p {
  font-size: 1em;
  line-height: 1.5em;
  padding: 0;
  margin: 0;
  flex: 1;
}
.project-pods .pod .pod-details div {
  text-align: right;
}
.project-pods .pod .pod-details div a {
  font-weight: bold;
}

.slider {
  position: relative;
}
.slider .slide {
  display: block;
  transition: 0.2s all linear;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}
.slider input {
  display: none;
}
.slider input:checked + .slide {
  opacity: 1;
}
.slider .slide-nav {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
}
.slider .slide-nav label {
  line-height: 1em;
  display: inline-block;
  color: #fff;
  font-size: 4rem;
  text-shadow: 0px 0px 1rem black;
  cursor: pointer;
}
.slider img {
  max-width: 100%;
  height: auto;
}
.slider input#slide-1:checked ~ .slide-nav .slide-label-1 {
  color: #8dca31;
}
.slider input#slide-2:checked ~ .slide-nav .slide-label-2 {
  color: #8dca31;
}
.slider input#slide-3:checked ~ .slide-nav .slide-label-3 {
  color: #8dca31;
}
.slider input#slide-4:checked ~ .slide-nav .slide-label-4 {
