}
.slider input#slide-8:checked ~ .slide-nav .slide-label-8 {
  color: #8dca31;
}
.slider input#slide-9:checked ~ .slide-nav .slide-label-9 {
  color: #8dca31;
}
.slider input#slide-10:checked ~ .slide-nav .slide-label-10 {
  color: #8dca31;
}
.slider input#slide-11:checked ~ .slide-nav .slide-label-11 {
  color: #8dca31;
