}
.slider input#slide-19:checked ~ .slide-nav .slide-label-19 {
  color: #8dca31;
}
.slider input#slide-20:checked ~ .slide-nav .slide-label-20 {
  color: #8dca31;
}

.template-project .banner_image {
  position: relative;
}
.template-project .banner_image .header {
  position: absolute;
  bottom: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
}
.template-project .banner_image .header h1 {
  margin-top: 0.5em;
}
.template-project .banner_image .header p {
  margin-bottom: 1em;
}
.template-project .slider {
  width: 100%;
  height: auto;
  max-width: 640px;
  max-height: 360px;
}
.template-project .slider .inner {
  width: 100%;
  padding-top: 56.25%;
}
.template-project .main-body {
  padding-top: 1em;
}
.template-project .header h1 {
  margin-bottom: 0;
}

:root {
  --max-width: 100%;
}

@media screen and (min-width: 576px) {
  :root {
    --max-width: 540px;
  }
}
@media screen and (min-width: 768px) {
  :root {
    --max-width: 720px;
  }
}
@media screen and (min-width: 992px) {
  :root {
    --max-width: 960px;
  }
}
@media screen and (min-width: 1200px) {
  :root {
    --max-width: 1140px;
  }
}
@media screen and (min-width: 1400px) {
  :root {
    --max-width: 1320px;
  }
}
.container, .block-InfoPod, nav.main ul {
  max-width: var(--max-width);
  margin: 0 auto;
}

.logo {
  max-width: 100%;
  height: auto;
}

body {
  padding: 0;
  margin: 0;
  background: #374244;
}
body.is-loading * {
  transition: none !important;
  animation: none !important;
}

body,
input,
select,
textarea {
  color: #f7f7f7;
  font-family: -apple-system, blinkmacsystemfont, segoe ui, helvetica, arial, sans-serif, apple color emoji, segoe ui emoji, segoe ui symbol;
  font-size: 16pt;
  font-weight: 300;
  line-height: 1.65em;
}

a {
  transition: color 0.2s ease-in-out, border-color 0.2s ease-in-out, opacity 0.2s ease-in-out;
  color: #8dca31;
  text-decoration: none;
  border-bottom: dotted 1px;
}
a.no-b {
  border-bottom: none;
}
a svg {
  transition: fill 0.2s ease;
}
a:hover {
  color: #8dca31;
  border-bottom-color: transparent;
}
a:hover svg {
  fill: #8dca31;
}

strong,
b {
  font-weight: 600;
}

em,
i {
  font-style: italic;
}

p,
ul,
ol,
dl,
table,
blockquote {
  margin: 0 0 2em 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: inherit;
  font-family: "Poppins", sans-serif;
  line-height: 1.75em;
  margin-bottom: 1em;
}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit;
  text-decoration: none;
}
h1 em,
h2 em,
h3 em,
h4 em,
h5 em,
h6 em {
  font-style: normal;
  font-weight: 300;
}

h1 {
  text-decoration: underline #8dca31;
}

h2 {
  font-size: 1.75em;
  letter-spacing: -0.025em;
}

h3 {
  font-size: 1.2em;
  letter-spacing: -0.025em;
}

sub {
  font-size: 0.8em;
  position: relative;
  top: 0.5em;
}

sup {
  font-size: 0.8em;
  position: relative;
  top: -0.5em;
}

hr {
  border-top: solid 1px #e0e0e0;
  border: 0;
  margin-bottom: 1.5em;
}

blockquote {
  border-left: solid 0.5em #e0e0e0;
  font-style: italic;
  padding: 1em 0 1em 2em;
}

section.special,
article.special {
  text-align: center;
}